body {
  margin: 0;
  margin-bottom:80px ;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.fot{
  display: flex;
  flex-direction: column;
  min-width: 100vh;
}

.page-footer{
 position: fixed;
 bottom: 0;
 left: 0;
 right: 0;
 height: 70px;
}

.conteiner{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  place-items: center;
  align-items: stretch;
  padding: 0;
  margin: 0;
  height: 40%;
}

.block{
  width: 60%;
  margin: 10px;
  text-align: center;
  box-sizing: border-box;
  border: 2px solid #766969;
  border-radius: 15px;
}

.item{
  position: relative;
  top: -25px;
}

.input{
  padding: 10px;
  max-width: 20%;
}

.container_footer-h6{
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: -4%;
  margin-left: 33%;
}

.text_p{
 position: relative;
 right: 460px;
 width: 100%;
 top: -20px;
 font-size: 0.8rem;
}
.footer-copyright{
  margin-block:-5px ;
}

.p{
  position: relative;
  top: 16px;
}